<template>
  <div class="pb-5">
      <div class="d-flex justify-content-around align-items-center p-1" >
      <button v-on:click="back" type="button" class="btn btn-lg btn-link">Вернуться назад</button>
      <button v-on:click="fetchText" type="button" onclick="blur()" class="btn btn-outline-secondary">Другой текст</button>
      <h3>{{ (i/time*60).toFixed()}}</h3>
      </div>
      <hr style="width:90%" class="m-auto">
      <p ref="text" class="text p-3" v-html="text.newArr">
      </p>
      
       <div class="progress mb-3" style="height:45px">
        <div class="progress-bar bg-success progress-bar-striped progress-bar-animated" role="progressbar" v-bind:style="{width:progress +'%'}" aria-valuemin="0" aria-valuemax="100"><span>{{Math.floor(progress)}}%</span></div>
        </div>
        
  </div>
  
</template>

<script>
export default {
    name:'TrainingStart',
    props: {
        text: {
            type: Object,
            required:true,
        }
    },
    data() {
        return {
            window:window,
            i:0,
            progress:0,
            time: 0,
            interval:{},
            mistakes:null,
            
            
        }
    },
    methods: {
        back() {
            this.$emit('back')
        },
        fetchText() {
            this.$emit('fetchText', this.text.n)
            this.i=0
            this.time=0
            clearInterval(this.interval)
            
        },
        press(e) {
          
            if (e.key!=='Shift' && e.key!=='Tab' && e.key!=='Control' && e.key!=='Alt' && e.key!=='CapsLock' && e.key!=='Meta' && e.key!=='Backspace') {
                if (this.$refs.text.childNodes[this.i].innerHTML===e.key) {
                    this.$refs.text.childNodes[this.i].classList.add('true')
                    this.i++
                } else {
                    this.mistakes++
                     this.$refs.text.childNodes[this.i].classList.add('false')
                }
            
                if (this.i===this.$refs.text.childNodes.length) {
                    
                    this.$emit('finish', [this.i,this.time,this.mistakes])
                }

                //отключение скролла на пробел
                 if ( ( e.keycode || e.which ) == 32) {
                    e.preventDefault();
                }

                //включить таймер
                if (this.i==1) {
                    this.setInt()
                }
             }
        },
        setInt() {
           this.interval =  setInterval(() => {
                this.time++
               
                
            }, 1000);
        }
        
    },
    watch: {
        i: function(val) {
            this.progress=val/this.$refs.text.childNodes.length*100
        }
    },
    
    mounted () {
        this.window.addEventListener('keydown',  this.press)
    },
    beforeUnmount() {
        this.window.removeEventListener('keydown',  this.press)
    }
}
</script>

<style>
.text {
    font-size: 2rem;
    font-weight: bold;
}
    .false { 
        color: red;
    }
.true {
    color: green;
}
.btn:focus {
  outline: none;
}
span {
    font-size: 2rem;
}
h3 {
    width: fit-content;
}
</style>